<template>
	<view>
		<view class="status" :style="{position:headerPosition,top:statusTop}"></view>
		<view class="header" :style="{position:headerPosition,top:headerTop}">
			<view class="addr"></view>
			<view class="input-box">
				
			</view>
			<view class="icon-btn">
				<view class="icon tongzhi"></view>
				<view class="icon setting" @tap="toSetting"></view>
			</view>
		</view>
		<!-- 占位 -->
		<view class="place"></view>
		<!-- 用户信息 -->
		<view class="user">
			<!-- 头像 -->
			<view class="left">
				<image :src="personInfo.avatarUrl" @tap="toSetting" v-if="personInfo.avatarUrl"></image>
				<!-- #ifdef MP-WEIXIN -->
				<!-- <button class="" open-type="getUserInfo" @getuserinfo="wxGetUserInfo" withCredentials="true">登陆</button> -->
					<button v-if="!personInfo.avatarUrl" class="userinfo-avatar" open-type="getUserInfo" @getuserinfo="wxGetUserInfo" withCredentials="true">						
					</button>
				<!-- #endif -->
			</view>
			<!-- 昵称,个性签名 -->
			<view class="right">
				<view class="username">{{personInfo.nickName?personInfo.nickName:'游客'}}</view>
				<view class="signature" @tap="toSetting">{{user.signature}}</view>
			</view>
			<!-- 二维码按钮 -->
			<view class="erweima" @tap="toMyQR">
				<view class="icon qr"></view>
			</view>
		</view>
		<!-- VIP banner -->
<!-- 		<view class="VIP">
			<view class="img">
				<image src="../../static/img/VIP.png"></image>
			</view>
			<view class="title">开通VIP会员</view>
			<view class="tis">会员特权</view>
		</view> -->
		<!-- 订单-余额 -->
		<view class="order">
			<!-- 订单类型 -->
			<view class="list">
				<view class="box" v-for="(row,index) in orderList" :key="index" @tap="toOrderList(index)">
					<view class="img">
						<view class="icon" :class="row.icon"></view>
					</view>
					<view class="text">{{row.text}}</view>
				</view>
			</view>
			<!-- 余额 -->
			<view class="balance-info">
				<view class="left">
					<view class="box">
						<view class="num">{{user.integral}}</view>
						<view class="text">积分</view>
					</view>
					<view class="box">
						<view class="num">{{user.envelope}}</view>
						<view class="text">佣金</view>
					</view>
					<view class="box">
						<view class="num">{{user.balance}}</view>
						<view class="text">余额</view>
					</view>
				</view>
				<view class="right">
					<view class="box" @tap="toDeposit">
						<view class="img">
							<view class="icon chongzhi"></view>
						</view>
						<view class="text">充值</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 工具栏 -->
		<view class="toolbar">		
			<view class="title">我的工具栏</view>
			<view class="list">
				<view class="box" v-for="(row,index) in mytoolbarList" :key="index" @tap="toPage(row.url)">
					<view class="img">
						<image :src="row.img"></image>
					</view>
					<view class="text">{{row.text}}</view>
				</view>
			</view>
		</view>
		<!-- 占位 -->
		<view class="place-bottom"></view>
		<y-loading id="y-loading" class="y-loading"></y-loading>
	</view>
</template>
<script>
import api from '@/common/api/request.js'
	export default {
		data() {
			return {
				isTrue:false,//是否存在登陆态
				personInfo:{},
				isfirst:true,
				headerPosition:"fixed",
				headerTop:null,
				statusTop:null,
				//个人信息,
				user:{
					username:'游客1002',
					face:'../../static/img/face.jpg',
					signature:'',
					integral:0,
					balance:0,
					envelope:0
				},
				// 订单类型
				orderList:[
					{text:'待付款',icon:"fukuan"},
					{text:'待发货',icon:"fahuo"},
					{text:'待收货',icon:"shouhuo"},
					{text:'待评价',icon:"pingjia"},
					{text:'退换货',icon:"tuihuo"}
				],
				// 工具栏列表
				mytoolbarList:[
					{url:'../../userPage/keep/keep',text:'我的收藏',img:'../../static/img/user/point.png'},
					{url:'../../userPage/coupon/coupon',text:'优惠券',img:'../../static/img/user/quan.png'}, 
					// {url:'',text:'新客豪礼',img:'../../static/img/user/renw.png'},
					// {url:'',text:'领红包',img:'../../static/img/user/momey.png'},
					// 
					{url:'../../userPage/address/address',text:'收货地址',img:'../../static/img/user/addr.png'},
					// {url:'',text:'账户安全',img:'../../static/img/user/security.png'},
					// {url:'',text:'银行卡',img:'../../static/img/user/bank.png'},
					// {url:'',text:'抽奖',img:'../../static/img/user/choujiang.png'},
					// {text:'客服',img:'../../static/img/user/kefu.png'},
					// {text:'签到',img:'../../static/img/user/mingxi.png'}
					
				]
			}
		},
		//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
		onPullDownRefresh() {
		    setTimeout(function () {
		        uni.stopPullDownRefresh();
		    }, 1000);
		},
		onPageScroll(e){
			//兼容iOS端下拉时顶部漂移
			this.headerPosition = e.scrollTop>=0?"fixed":"absolute";
			this.headerTop = e.scrollTop>=0?null:0;
			this.statusTop = e.scrollTop>=0?null:-this.statusHeight+'px';
		},
		onLoad() {
			this.statusHeight = 0;
			// #ifdef APP-PLUS
			this.statusHeight = plus.navigator.getStatusbarHeight();
			// #endif
			
		},
		onReady(){
			
		},
		onShow(){
			uni.getStorage({
				key: 'skey',
				success: (res)=>{ //session存在skey，执行1
					this.toCheckSession();
				},
				fail:(e)=>{
					//登陆.....tongshang
				}
			});
			
		},
		methods: {
			toCheckSession(){
				uni.checkSession({
					success: (res) => {
						this.isTrue = true;
						console.log('直接拿openId')
						this.toGetUserData();
					},
					fail: (err) => {//登陆态过期
						this.toLogin();
						//登陆=》登陆后通过code获取session_key 转换成skey存入storage
					}
				});
			},
			wxGetUserInfo(res){
				if (!res.detail.iv) {
					uni.showToast({
						title: "您取消了授权,登录失败",
						icon: "none"
					});
					return false;
				}
				console.log('-------用户授权，并获取用户基本信息和加密数据------')
				this.personInfo = res.detail.userInfo;
				this.toLogin();

			},
			toOrderList(index){
				uni.navigateTo({url:'../../userPage/order_list/order_list?tbIndex='+index}) 
			},
			toSetting(){
				uni.navigateTo({
					url:'../../userPage/setting/setting'
				})
			},
			toMyQR(){
				uni.navigateTo({
					url:'../../userPage/myQR/myQR'
				})
			},
			toGetUserData(){
				uni.getUserInfo({
					success: (c) => {
						this.personInfo = c.userInfo;
					}
				});
			},
			toLogin(){
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						console.log('-------获取code-------');
						console.log(loginRes.code);
						this.getSkeyAndOpenId(loginRes.code);
						this.isTrue = true;
					}.bind(this)
				});
			},
			getSkeyAndOpenId(code){
				// this.$yLoading().show();
				this.$postRequest({
					url: 'EShop/login',
					data: { jsCode:code },
					hasToken:false,
					allSuccess: res => {
						uni.setStorage({
							key:'skey',
							data:res.session_key
						});
						uni.setStorage({
							key:'token',
							data:res.token
						});
					}
				});

				// let secret = '119afffd251e8b1b6e0f0a96e977bb7d';
				// let appid = 'wx0904846d9b4c40fe';

			},
			saveStorage(name,data){
				uni.setStorage({
					key: name,
					data: data,
					fail: (err) => {
						console.log(err);
					}
				});
			},
			isLogin(){
				//实际应用中,用户登录状态应该用token等方法去维持登录状态.
				const value = uni.getStorageSync('UserInfo');
				if (value) {
					return true;
				}
				return false
			},
			toDeposit(){
				if(this.isTrue){
					uni.navigateTo({
						url:'../../userPage/deposit/deposit'
					});
				}else{
					uni.showToast({
						title:'请先登陆',
						icon:'none'
					});
				}
			},
			toPage(url){
				if(this.isTrue){
					uni.navigateTo({
						url:url
					});
				}else{
					uni.showToast({
						title:'请先登陆',
						icon:'none'
					});
				}
				
			}
		}
	} 
</script>
<style lang="scss">
	page{position: relative;}
	@font-face {font-family:"HMfont-home";src:url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');}
	.icon {
		font-family:"HMfont-home" !important;
		font-size:60upx;
		font-style:normal;
		color:#000000;
		&.setting {
			&:before{content:"\e64e";}
		}
		&.tongzhi {
			&:before{content:"\e729";}
		}
		&.qr {
			&:before{content:"\e600";}
		}
		&.chongzhi {
			&:before{content:"\e648";}
		}
		&.fukuan {
			&:before{content:"\e67f";}
		}
		&.fahuo {
			&:before{content:"\e680";}
		}
		&.shouhuo {
			&:before{content:"\e6b1";}
		}
		&.pingjia {
			&:before{content:"\e66b";}
		}
		&.tuihuo {
			&:before{content:"\e66c";}
		}
	}
	.status {
		width: 100%;
		height: 0;
		position: fixed;
		z-index: 10;
		background-color: #8bbce7;
		top: 0;
		/*  #ifdef  APP-PLUS  */
		height: var(--status-bar-height);//覆盖样式
		/*  #endif  */
		
	}
	
	.header{
		width: 92%;
		padding: 0 4%;
		height: 100upx;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		position: fixed;
		top: 0;
		z-index: 10;
		background-color: #8bbce7;
		/*  #ifdef  APP-PLUS  */
		top: var(--status-bar-height);
		/*  #endif  */
		.icon-btn{
			width: 120upx;
			height: 60upx;
			flex-shrink: 0;
			display: flex;
			.icon{
				color: #fff;
				width: 60upx;
				height: 60upx;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				font-size: 42upx;
			}
		}
	}
	.place{
		background-color: #8bbce7;
		height: 100upx;
		/*  #ifdef  APP-PLUS  */
		margin-top: var(--status-bar-height);
		/*  #endif  */
	}
	.place-bottom{
		height: 300upx;
	}
	.user{
		width: 92%;
		padding: 0 4%;
		display: flex;
		align-items: center;
		// position: relative;
		background-color: #8bbce7;
		padding-bottom: 120upx;
		.left{
			width: 20vw;
			height: 20vw;
			flex-shrink: 0;
			margin-right: 20upx;
			border: solid 1upx #fff;
			border-radius: 100%;
			image{
				width: 20vw;
				height: 20vw;
				border-radius: 100%;
			}
			
		}
		.right{
			width: 100%;
			.username{
				font-size: 36upx;
				color: #fff;
			}
			.signature{
				color: #eee;
				font-size: 28upx;
			}
		}
		.erweima{
			flex-shrink: 0;
			width: 10vw;
			height: 10vw;
			margin-left: 5vw;
			border-radius: 100%;
		
			display: flex;
			justify-content: center;
			align-items: center;
			background: linear-gradient(to left, #fbbb37 0%,#fcf0d0 105%);
			.icon{
				color: #7b6335;
				font-size: 42upx;
			}
		}
	}
	.order{
		width: 84%;
		margin: 30upx 4% 30upx 4%;
		padding: 30upx 4% 20upx 4%;
		background-color: #fff;
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.1);
		border-radius: 15upx;
		.list{
			display: flex;
			border-bottom: solid 1upx #17e6a1;
			padding-bottom: 10upx;
			.box{
				width: 20%;
				.img{
					width: 100%;
					display: flex;
					justify-content: center;
					.icon{
						font-size: 50upx;
						color: #464646;
					}
				}
				.text{
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 28upx;
					color: #3d3d3d;
				}
			}
		}
		.balance-info{
			display: flex;
			padding: 10upx 0;
			.left{
				width: 75%;
				display: flex;
				.box{
					width: 50%;
					font-size: 28upx;
					
					.num{
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
						color: #f9a453;
					}
					.text{
						width: 100%;
						display: flex;
						justify-content: center;
						color: #3d3d3d;
						font-size: 28upx;
					}
				}
			}
			.right{
				border-left: solid 1upx #17e6a1;
				width: 25%;
				.box{
					
					.img{
						width: 100%;
						height: 50upx;
						display: flex;
						justify-content: center;
						align-items: flex-end;
						.icon{
							font-size: 45upx;
							color: #e78901;
						}
					}
					.text{
						width: 100%;
						display: flex;
						justify-content: center;
						font-size: 28upx;
						color: #3d3d3d;
					}
				}
			}
		}
	}
	.VIP{
		width: 84%;
		margin: -65upx auto 20upx auto;
		padding: 30upx 4%;
		background: linear-gradient(to left, #dea96d 0%,#f6d59b 100%);
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.2);
		border-radius: 15upx;
		display: flex;
		align-items: center;
		.img{
			flex-shrink: 0;
			width: 60upx;
			height: 60upx;
			image{
				width: 60upx;
				height: 60upx;
			}
		}
		.title{
			width: 100%;
			color: #796335;
			font-size: 30upx;
		}
		.tis{
			width: 100%;
			display: flex;
			justify-content: flex-end;
			color: #fcf0d0;
			font-size: 26upx;
		}
	}
	.toolbar{
		width: 92%;
		margin: 0 4% 0 4%;
		padding: 0 0 20upx 0;
		background-color: #fff;
		box-shadow: 0upx 0upx 25upx rgba(0,0,0,0.1);
		border-radius: 15upx;
		.title{
			padding-top: 10upx;
			margin: 0 0 10upx 3%;
			font-size: 30upx;
			height: 80upx;
			display: flex;
			align-items: center;
		}
		.list{
			display: flex;
			flex-wrap: wrap;
			.box{
				width: 25%;
				margin-bottom: 30upx;
				.img{
					width: 23vw;
					height: 10.5vw;
					display: flex;
					justify-content: center;
					
					image{
						width: 9vw;
						height: 9vw;
					}
				}
				.text{
					width: 100%;
					display: flex;
					justify-content: center;
					font-size: 26upx;
					color: #3d3d3d;
				}
			}
		}
	}
	.userinfo-avatar {  
	  overflow:hidden;  
	  display: block;  
	  width: 150rpx;  
	  height: 150rpx;
	  background: #eee;
	  border-radius: 50%;  
	  border: 2px solid #fff;
	  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2);  
	}  
</style>
